import React, { useMemo } from 'react';
import { useMeasure, useWindowSize } from 'react-use';

import Box from './Box'

const FullpageVerticalCenter = ({ children, measureHeight, ...props }) => {
  // const { hideHeader } = useContext(headerContext)
  const windowSize = useWindowSize()
  const [measureRef, { height }] = useMeasure()
  const shouldCenter = useMemo(() => height < windowSize.height, [height, windowSize.height])
  return (
    <Box
      height="100%"
      {...props}
    >
      <Box position="relative" height="100%">
        <Box
          className="vertical-center-container"
          position={shouldCenter ? 'absolute' : 'static'}
          top="50%"
          width="100%"
          transform={shouldCenter && 'translateY(-50%)'}
          height="100%"
          ref={measureRef}
        >
          {children}
        </Box>
      </Box>
    </Box>
  )
};

export default FullpageVerticalCenter;
