import React from 'react'
import { useToken } from '@chakra-ui/react';

import Box from '../components/Box';
import Flex from '../components/Flex';
import GoShare from '../components/Logos/GoShare';
import PaintLink from '../components/PaintLink';

const Header = ({ onOpen, headerColor, logoWidth, ...props }) => {
  // console.log(headerColor)
  const [teal] = useToken('colors', ['custom.teal'])
  return (
    <Flex
      as="header"
      position="absolute"
      bg="transparent"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent="center"
      zIndex="docked"
      overflow="hidden"
      mx="0.5em"
      mt="1em"
      {...props}
    >
      <Box.Relative width={logoWidth || '5.625em'}>
        <PaintLink to="/" hex={teal}>
          <GoShare color={headerColor ? 'white' : 'custom.teal'} />
        </PaintLink>
      </Box.Relative>
    </Flex>
  )
}

export default Header
