import React from 'react'

import Box from '../components/Box'
// import { responsive } from '../components/ThemeProvider/theme'
import Gogoro from '../components/Logos/Gogoro'

const Footer = ({ logoWidth, className, ...props }) => {
  return (
    <Box
      textAlign="center"
      className={`${className || ''} footer`}
      // pt={responsive('1.625rem', '2.375em')}
      // pb={responsive('3.75rem', '3.625em')}
      {...props}
    >
      <Gogoro width={logoWidth || '4.375em'} />
    </Box>
  )
}

export default Footer
