import React from 'react'
import PropTypes from 'prop-types'
import Helmet from 'react-helmet'
import { useStaticQuery, graphql, withPrefix } from 'gatsby'

import Box from '../../components/Box';
import theme from '../../components/ThemeProvider/theme';

import Header from '../Header'
import Footer from '../Footer';
import FullpageVerticalCenter from '../../components/FullpageVerticalCenter';

const Layout = ({
  noHeader,
  children,
  noPt,
  bg,
  headerColor,
  title: pageTitle,
  footerProps,
  headerProps,
  logoWidth,
  ...props
}) => {
  const { site: { siteMetadata: { title, description, url } } } = useStaticQuery(graphql`
    query LayoutQuery  {
      site {
        siteMetadata {
          title
          description
          url
        }
      }
    }
  `)
  return (
    <>
      <Helmet titleTemplate={`${title} - %s`} defaultTitle={title}>
        <html lang="zh-Hant-TW" />
        <link rel="shortcut icon" href={withPrefix('/favicon.ico')} />
        <link rel="apple-touch-icon-precomposed" href={withPrefix('/favicon.png')} />
        {pageTitle && <title>{pageTitle}</title>}
        <meta name="description" content={description} />
        <meta name="theme-color" content="#06f3f9" />
        <meta property="og:url" content={url} />
        <meta property="og:title" content="測測你的命定度假勝地！" />
        <meta property="og:description" content="玩超準心理測驗，騎 GoShare 再抽歐美雙人機票！" />
        <meta property="og:image" content={`${url}/fb_share.png`} />
        <meta property="og:image:type" content="image/png" />
        <meta name="twitter:card" content="summary" />
      </Helmet>
      <FullpageVerticalCenter>
        {!noHeader && <Header headerColor={headerColor} height={theme.headerHeight} {...headerProps} />}
        <Box as="main" pt={!noHeader && (!noPt && theme.headerHeight)} position="relative" bg={bg} height="100%" {...props}>
          {children}
          <Footer {...footerProps} />
        </Box>
      </FullpageVerticalCenter>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
